import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';

const styles = {
  paper: {
    padding: 'theme.spacing.unit',
    textAlign: 'center',
    color: 'theme.palette.text.secondary',
  },
  mainFeatureOutterBox1: {
    margin: '15vh 0% 15vh 10%',
    border: '1px black solid',
    height: '35vh',
  },
  mainFeatureOutterBox2: {
    margin: '15vh 5% 15vh 5%',
    border: '1px black solid',
    height: '35vh',
  },  
  mainFeatureOutterBox3: {
    margin: '15vh 10% 15vh 0%',
    border: '1px black solid',
    height: '35vh',
  }
};

function FormRow(props) {
  const { classes } = props;

  return (
    <React.Fragment>
      <Grid item xs={12} sm={4}>
        <div className={classes.mainFeatureOutterBox1}>
          <Typography variant="h6" color="inherit" className={classes.paper}>item</Typography>
        </div>
      </Grid>
      <Grid item xs={12} sm={4}>
        <div className={classes.mainFeatureOutterBox2}>
          <Typography variant="h6" color="inherit" className={classes.paper}>item</Typography>
        </div>
      </Grid>
      <Grid item xs={12} sm={4}>
        <div className={classes.mainFeatureOutterBox3}>
          <Typography variant="h6" color="inherit" className={classes.paper}>item</Typography>
        </div>
      </Grid>
    </React.Fragment>
  );
}

FormRow.propTypes = {
  classes: PropTypes.object.isRequired,
};

function MainFeatures(props) {
  const { classes } = props;

  return (

      <Grid container spacing={8}>
        <Grid container item xs={12} spacing={24}>
          <FormRow classes={classes} />
        </Grid>
      </Grid>

  );
}

MainFeatures.propTypes = {
  classes: PropTypes.object.isRequired,
};

  export default withStyles(styles)(MainFeatures);